import React, { useState } from "react";
import HeaderNav from "@/app/components/common/HeaderNav";
import CustomImage from "@/app/components/ui/CustomImage";

export default function page() {
  const [inputFields, setInputFields] = useState({
    fromCurrency: "RUB",
    toCurrency: "USDT",
  });

  return (
    <div className="wrapper">
      <HeaderNav></HeaderNav>
      <div className="flex w-full h-full bg-[#1b1b1b] pt-[30px] font-nekstmedium">
        <div className="w-[20%] h-[100%]"></div>
        <div className="w-[80%] h-[100%]">
          <div className="w-full font-nekstregular">
            Capico / P2P trading / Post an ad
          </div>
          <p className="text-[34px] mt-[15px]">Post an ad</p>
          <div className="">
            <div className="flex mt-[40px] border-b-[1px] border-solid border-[#333]">
              <button className="pb-[10px] text-[20px] border-b-[2px] border-solid border-white mr-[10px]">
                I want to buy
              </button>
              <button className="pb-[10px] text-[20px] border-b-[0px] border-solid border-white mr-[10px]">
                I want to sell
              </button>
            </div>
            <form action="">
              <div className="grid grid-[1fr_1fr] grid-flow-col max-w-[80%] font-nekstlight mt-[10px]">
                <div className="">
                  {/* <label htmlFor="font">Crypto</label> */}
                  <div className="relative w-[70%] group ">
                    <input
                      type="checkbox"
                      id="dropdownToggle"
                      className="hidden peer "
                    />
                    <label
                      htmlFor="dropdownToggle"
                      className="text-white w-[100%] h-[35px] rounded-[20px] cursor-pointer border-[1px] border-solid flex items-center border-[#414141] justify-end group-hover"
                    >
                      <div className="flex items-center justify-end pr-[10px] w-[10%] h-full ">
                        <CustomImage
                          width={20}
                          height={20}
                          alt="arrow"
                          src="/pages/p2p-trade/arrow.svg"
                          className="invert transition-transform duration-300 group-hover:rotate-[180deg]"
                        />
                      </div>
                    </label>
                    <div className="absolute left-0 top-[28px]   mt-2 w-[100%] bg-[#212121] rounded-lg  group-hover:opacity-100 opacity-100 duration-300 justify-center flex flex-wrap p-[8px]">
                      <button className="w-[100%] py-[20px] hover:bg-[#444] rounded-[5px] flex">
                        <div className="w-[20%] ">
                          <CustomImage
                            width={20}
                            height={20}
                            alt="USDT"
                            src="/pages/p2p-trade/arrow.svg"
                          ></CustomImage>
                        </div>
                      </button>
                    </div>
                  </div>
                </div>
                <div>2</div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
